.beforeMaking-area,
.afterMaking-area {
  width: 550px;
  height: 550px;
  margin: 0;
  background-color: rgb(224, 225, 231);
  border-radius: 5px;
  @media (max-width: 600px) {
    width: 100%;
  }

  .orderBoard {
    padding: 10px 10px 40px 10px;
    position: relative;
    overflow: scroll;
    height: 100%;
  }
  h2 {
    margin-top: 0;
  }
}
